<template>
  <div>
    <div class="register">
      <van-nav-bar
        title="注册"
        left-text="登录"
        right-text="关闭"
        @click-left="goPage('Login')"
        @click-right="goPage('Home')"
      />
      <div class="logo">
        <img class="auto-img" src="../assets/images/logo.png" alt />
      </div>

      <van-form @submit="register">
        <van-field label="用户名" maxlength="10" v-model="userInfo.nickName" />

        <van-field label="手机号" maxlength="11" type="tel" v-model="userInfo.phone" />

        <van-field label="密码" maxlength="12" type="password" v-model="userInfo.password" />

        <div style="margin: 16px;">
          <van-button round block type="info" native-type="submit">注册</van-button>
        </div>
      </van-form>
    </div>
  </div>
</template>

<script>
// 导入验证模块
import validForm from "../assets/js/validForm.js";

export default {
  data() {
    return {
      // 用户注册信息
      userInfo: {
        nickName: "",
        phone: "",
        password: ""
      }
    };
  },

  methods: {
    // 页面切换
    goPage(name) {
      this.$router.push({ name });
    },

    // 发起注册请求
    register() {
      // 验证注册信息
      if (validForm.validUserForm(this.userInfo)) {
        // 验证数据时显示提示 轻提示组件 注意组件在script运用时要用this.$,首字母小写
        this.$toast.loading({
          mask: true,
          message: "加载中...",
          forbidClick: true, // 禁用背景点击

          //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
          duration: 0
        });

        // 储存对象
        let data = Object.assign({}, this.userInfo);

        data.appkey = this.appkey;

        // 发起注册请求
        this.axios({
          method: "POST",
          url: "/register",

          //post传递参数需要写在data
          data
        })
          .then(result => {
            //关闭加载提示
            this.$toast.clear();
            // 
            if (result.data.code == 100) {
              // 注册成功
              this.$router.push({ name: "Login" });
            } else if (result.data.code == 102) {
              //已被注册
              this.$router.push({ name: "Login" });
            } else {
              // 注册失败
              this.$toast.fail("账号失败");
            }
          })
          .catch(err => {
            //关闭加载提示
            this.$toast.clear();
            // 
          });
        // 
      }
    }
  }
};
</script>

<style lang="less" scoped>
.register {
  text-align: center;
  .logo {
    padding-top: 10px;
    img {
      width: 200px;
    }
  }
}
/deep/ .van-nav-bar {
  background-color: #36200c;
}
/deep/.van-nav-bar__title {
  color: #fff;
}
/deep/ .van-nav-bar__text {
  color: #fff;
}
/deep/ .van-button--info {
  background-color: #36200c;
  border: 1px solid #36200c;
}
</style>